import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Navbar from "../components/navbar"
import Heading from "react-bulma-components/esm/components/heading"
import Container from "react-bulma-components/esm/components/container"
import Section from "react-bulma-components/esm/components/section"
import { Link, graphql, useStaticQuery } from "gatsby"
import NewsletterSubscribe from "../components/newsletterSubscribe"

const Danke = () => {
      const data = useStaticQuery(graphql`
    query {
      allNewJson(sort: { fields: published, order: DESC }, limit: 1) {
        edges {
          node {
            published
            title
            slug
            tags
            newsletter {
              welcome
              intro
              parts {
                emoji {
                  label
                  icon
                }
                title
                tags
                text
              }
              outro
            }
          }
        }
      }
    }
  `)
  return (
    <Layout>
      <Seo
        title="Danke für die Anmeldung"
        keywords={[`ecotainment`, `newsletter`, `danke`]}
      />
      <Container>
        <Navbar />
        <NewsletterSubscribe />
        <Section>
          <Heading>Danke für die Anmeldung!</Heading>
          Hier ist der letzte Newsletter, schonmal zum reinstöbern:{' '}
          {data.allNewJson.edges.map(({ node }, index) => (
            <Link key={index} to={`/${node.slug}`}>
              {node.title}
            </Link>
          ))}
        </Section>
      </Container>
    </Layout>
  )
}
export default Danke
